//  
export default function prepareForShow(array) {
  if (array.length === 0) {
    // answer = false; //      
    return;
  }
  if (array.genres.length === 0) {
    array.genres[0] = 'Unknown'; //   
  }
  if (array.genres.length > 3) {
    array.genres = array.genres.slice(0, 2); //   ;-))
    array.genres[2] = 'Other';
  }
  array.genreShow = array.genres.join(', '); //  
  array.release_year = array.release_date.substring(0, 4); //   

  // answer = true;
  return array;
}
